/* unplugin-vue-components disabled */import { onMounted, reactive, ref } from 'vue';
import validateSchema from '@/utils/validate-schema';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';
export default {
  name: 'Login',

  setup() {
    //控制音乐播放
    const musicShow = ref(true); //音乐

    const Music = ref(null); //控制音乐播放

    const musicPlay = () => {
      if (musicShow.value === true) {
        Music.value.play();
      } else {
        Music.value.pause();
      }
    };

    onMounted(() => {
      //因为移动端限制了自动播放 需要用户进行交互才能进行播放
      //所以给容器增加点击事件 当用户点击时 开始播放
      //播放后 删除事件
      backgroundContainer.value.onmousedown = () => {
        Music.value.play();
        backgroundContainer.value.onmousedown = null;
      };
    }); //存储表单信息

    const userForm = reactive({
      zhanghao: 'tianhuaban2',
      mima: '112233'
    }); //获取表单

    const userFormRef = ref(null);
    const router = useRouter();
    const store = useStore(); //背景容器盒子

    const backgroundContainer = ref(null); //控制图片

    let backgroundNumber = ref(null); //随机选择1-9当中的图片

    backgroundNumber.value = Math.round(Math.random() * (9 - 1)) + 1;
    onMounted(() => {
      //赋值图片
      backgroundContainer.value.style.backgroundImage = `url('http://yunqi-xiuxian.oss-cn-hangzhou.aliyuncs.com/xiuxian_login_${backgroundNumber.value}.jpg')`;
    }); //校验规则

    const loginRules = {
      zhanghao: [{
        required: true,
        trigger: 'blur',
        message: '用户名不能为空'
      }, {
        trigger: 'blur',
        validator: validateSchema.validateAccount
      }],
      mima: [{
        required: true,
        trigger: 'blur',
        message: '密码不能为空'
      }, {
        trigger: 'blur',
        validator: validateSchema.validatePassword
      }]
    }; //登录

    const login = () => {
      userFormRef.value.validate(async valid => {
        //如果校验通过
        if (valid) {
          try {
            //发送登录请求
            await store.dispatch('user/login', userForm); //发送请求 获取用户token

            await store.dispatch('user/getUserToken', userForm); //添加进已登录列表

            store.dispatch('user/addAccountArrAction'); //提示登录成功

            ElMessage.success('登录成功'); //页面进行跳转

            router.push('/home');
          } catch (error) {
            //方便测试
            console.log(error);
            ElMessage.error(error.message);
          }
        }
      });
    };

    return {
      musicShow,
      userForm,
      loginRules,
      login,
      userFormRef,
      backgroundContainer,
      musicPlay,
      Music
    };
  }

};